<script>
    export default {
        data: function() {
            return {
                align: "top",
                index: 0,
                alignChoose: ["top", "middle", "bottom"],
                gutter: 0,
                colCount: 2,
                renderCode: '<v-row>\n' +
                            '    <v-col span="12"></v-col>\n' +
                            '    <v-col span="12"></v-col>\n' +
                            '</v-row>',
                gutterOptions: [
                    {label: '0px', value: 0},
                    {label: '8px', value: 8},
                    {label: '16px', value: 16},
                    {label: '24px', value: 24},
                    {label: '32px', value: 32},
                    {label: '40px', value: 40},
                    {label: '48px', value: 48}
                ],
                colCountOptions: [
                    {label: '2', value: 2},
                    {label: '3', value: 3},
                    {label: '4', value: 4},
                    {label: '6', value: 6},
                    {label: '8', value: 8},
                    {label: '12', value: 12}
                ],
            }
        },
        methods: {
            alignChange() {
                this.index = (this.index + 1) % 3;
                this.align = this.alignChoose[this.index];
            },
            gutterChange(value) {
                this.refreshCode(value, this.colCount);
            },
            colCountChange(value) {
                this.refreshCode(this.gutter, value);
            },
            refreshCode(gutter, colCount) {
                this.renderCode = '';
                let gutterStr = "";
                if (gutter) gutterStr = ' gutter="' + gutter + '"';
                this.renderCode += '<v-row' + gutterStr + '>\n';
                for (let n = 0; n < colCount; n++) {
                    this.renderCode += '    <v-col span="' + (24 / colCount) + '"></v-col>\n';
                }
                this.renderCode += '</v-row>';
            }
        }
    }
</script>


